.sidenav {
    height: 100%;
    width: 250px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    background-color: #282c34;
    overflow-x: hidden;
    /* Disable horizontal scroll */
    padding-top: 20px;
}

li {
    height: 50px;
    list-style: none;
}

.sidenav a {
    text-decoration: none;
    font-size: 25px;
    color: azure;
    display: flex;
    transition: 0.3s;
}

.sidenav a:hover {
    color: rgb(191, 191, 191);
}


.sidenav .profile-img {
    display: block;
    margin-left: 40px;
    margin-right: 40px;
}

.sidenav .profile-img a {
    padding-left: 30px;
}

.sidenav .nav-links {
    height: 65%;
}

.contact-links {
    margin-left: 40px;
    margin-right: 40px;
}

.contact-links ul {
    justify-content: space-evenly;
    display: flex;
    list-style: none;
    padding-left: 0;
}

.contact-links a {
    color: azure;
    padding: 7px;
    text-decoration: none;
}

.contact-links a :hover {
    color: rgb(191, 191, 191);
}