.work-experience {
    height: 100vh;
    justify-content: start;
    text-align: left;
    margin-left: 50px;
    margin-right: 50px;
}

.work-experience h4 {
    font-size: 20px;
    font-weight: 700;
    line-height: 25px;
    margin-bottom: 0px;
}

.work-experience p {
    text-align: justify;
    text-justify: inter-word;
    margin-top: 0px;
    font-size: 13px;
    line-height: 20px;
}